import MxDatePicker from "@/components/mx-datepicker/mx-datepicker.vue";
export default {
	components: {
		MxDatePicker
	},
	data() {
		return {
			isColor: 'rgba(255,255,255,0.6)',
			activeColor: 'rgb(255,255,255)',
			isLength: '',
			total: '',
			allId: '',
			startdate: '',
			datetime: '',
			enddate: '',
			area: ['选择地址', '', ''],
			type: 'datetime',
			showPickerStart: false, //显示选择开始时间组件
			showPickerEnd: false, //显示选择结束时间组件
			value: ''
		}
	},
	computed: {
		isModel: function() {
			return this.$store.state.model;
		},
		cart: function() {
			let cart = this.$store.state.cart;
			let _this = this;
			_this.isLength = Math.ceil(cart.length / 3);
			//console.log(this.isLength)
			return cart
		},
		isWidth: function() {
			return this.$store.state.windowWidth
		},
		istotal: function() {
			let cart = this.$store.state.cart;
			let _this = this;
			let price = [];
			for (let i in cart) {
				price.push(cart[i].price)
			}
			var s = 0;
			for (var i = price.length - 1; i >= 0; i--) {
				s += price[i];
			}
			_this.total = s;
			return s
		},
		getNow: function() {
			var date = new Date();
			var seperator1 = "-";
			var year = date.getFullYear();
			var month = date.getMonth() + 1;
			var strDate = date.getDate();
			if (month >= 1 && month <= 9) {
				month = "0" + month;
			}
			if (strDate >= 0 && strDate <= 9) {
				strDate = "0" + strDate;
			}
			var currentdate = year + seperator1 + month + seperator1 + strDate;
			console.log(currentdate)
			return currentdate;
		}
	},
	onReady() {
		let openId = uni.getStorageSync('openid');
		let _this = this;
		uni.request({
			url: _this.$store.state.baseurl + '/order/findShopping',
			data: {
				openId: openId
			},
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: function(res) {
				console.log(res)
				let cart = res.data.data;
				_this.$store.commit('setCart', cart);
				//console.log(_this.$store.state.cart)
			}
		})
	},
	methods: {
		isDelete: function(id, name) {
			let openId = uni.getStorageSync('openid');
			let _this = this;
			uni.showModal({
				title: '提示',
				content: name + '   将被删除',
				success: function(res) {
					if (res.confirm) {
						//console.log('用户点击确定');
						uni.request({
							url: _this.$store.state.baseurl + '/order/delOne',
							data: {
								openId: openId,
								userId: id
							},
							method: 'POST',
							header: {
								'content-type': 'application/x-www-form-urlencoded'
							},
							success: function(res) {
								if (res.data.success == true) {
									//console.log(res.data)
									let cart = res.data.data;
									_this.$store.commit('setCart', cart);
								} else {
									uni.showModal({
										title: '提示',
										content: '删除失败',
										success: function(res) {
											if (res.confirm) {
												//console.log('用户点击确定');
											} else if (res.cancel) {
												//console.log('用户点击取消');
											}
										}
									});
								}
							}
						})
					} else if (res.cancel) {
						//console.log('用户点击取消');
					}
				}
			});
		},
		// // 选择开始时间
		// onShowDatePickerStart(type) { //显示
		// 	this.type = type;
		// 	this.showPickerStar = true;
		// 	this.value = this[type];
		// },
		// // 选择结束时间
		// onShowDatePickerEnd(type) { //显示
		// 	this.type = type;
		// 	this.showPickerEnd = true;
		// 	this.value = this[type];
		// },
		// // 选定开始
		// onSelected(e) { //选择
		// 	this.showPickerStart = false;
		// 	if (e) {
		// 		this[this.type] = e.value;
		// 		this.startdate = this[this.type]
		// 		console.log("dateTime", this.startdate);
		// 	}
		// },
		// 选定结束
		onSelectedEnd(e) { //选择
			this.showPickerEnd = false;
			if (e) {
				this[this.type] = e.value;
				this.enddate = this[this.type]
				console.log("dateTime", this.enddate);
			}
		},
		startChange: function(e) {
			this.startdate = e.target.value
		},
		chooseArea: function(e) {
			this.area = e.detail.value
		},
		endChange: function(e) {
			this.enddate = e.target.value
		},
		submitOrder: function(e) {

			let cart = this.$store.state.cart;
			let _this = this;
			let allId = '';

			// if(cart.length==0){
			// 	uni.showModal({
			// 		title: '提示',
			// 		content: '请选择所需人员'
			// 		})
			// 	return false
			// }

			for (let i in cart) {
				if (i == 0) {
					allId = cart[i].userId
				} else {
					allId = allId + '-' + cart[i].userId
				}
			}


			let total = this.total;
			let openId = uni.getStorageSync('openid');
			e.detail.value.orderPrice = total;
			e.detail.value.openId = openId;
			e.detail.value.orderUserId = allId;
			e.detail.value.address = this.area[0] + this.area[1] + this.area[2];
			e.detail.value.activityBeginTime = this.startdate;
			e.detail.value.activityAfterTime = this.enddate;
			e.detail.value.activityAddress = e.detail.value.address + e.detail.value.xiang
			// console.log(allId)
			//console.log(e.detail.value)

			//console.log(e.detail.value.dockingPeopleName)
			//判断是否填写数据
			console.log(e)
			if (e.detail.value.address == '选择地址' || e.detail.value.xiang == '' || e.detail.value.dockingPeopleName == '' || e.detail
				.value.dockingPeoplePhone == '' || e.detail.value.activityName == '' || e.detail.value.activityBeginTime == '' ||
				e.detail.value.activityAfterTime == '') {
				uni.showModal({
					title: '提示',
					content: '请填写完整信息'
				})
			} else if (cart.length == 0) {
				uni.showModal({
					title: '提示',
					content: '购物车不能为空'
				})
			} else {

				uni.showModal({
					title: '提示',
					content: '确认提交',
					success: function(res) {
						if (res.confirm) {
							//console.log('用户点击确定');
							uni.showLoading({
								content: '提交中'
							})
							uni.request({
								url: _this.$store.state.baseurl + '/order/createOrder',
								data: e.detail.value,
								method: 'POST',
								header: {
									'content-type': 'application/x-www-form-urlencoded'
								},
								success: function(res) {
									if (res.data.data == 1) {
										let cart = '';
										_this.$store.commit('setCart', cart);
										uni.hideLoading()
										uni.showModal({
											title: '提交成功',
											content: '稍后会有公司人员与你联系......',
											success: function(res) {
												if (res.confirm) {
													console.log('用户点击确定');
												} else if (res.cancel) {
													console.log('用户点击取消');
												}
											}
										});
										setTimeout(function() {
											_this.$store.commit('change_page', 0)
											uni.navigateTo({
												url: '/pages/index/index'
											})
										}, 1000)
									}
								}
							})
						} else if (res.cancel) {
							//console.log('用户点击取消');
						}
					}
				});

			}
			// else if (!(/^[1](([3|5|8][\\d])|([4][4,5,6,7,8,9])|([6][2,5,6,7])|([7][^9])|([9][1,8,9]))[\\d]{8}$/.test(e.detail.value.dockingPeoplePhone))){
